<template>
  <div>
    <div class="headerZINDEX">
      <mu-appbar style="width: 100%;" :color="$store.state.color" class="header">
        <a @click="()=>{
            $router.go(-1)
          }">
          <mu-button icon slot="left" class="icon" size="20">
            <mu-icon value="keyboard_arrow_left"></mu-icon>
          </mu-button>
        </a>
        <span>日志管理</span>
      </mu-appbar>
    </div>
    <div class="containerListContent border headerSearchContent">
      <div class="imgSizeDaily">
        <input type="text" id placeholder="请输入您想筛选的内容" v-model="searchValue" />
        <img src="static/img/search.png" alt @click="search()" />
      </div>
    </div>
    <mu-container ref="container" class="demo-loadmore-content">
    <mu-load-more @refresh="refresh" :refreshing="refreshing" :loading="loading" @load="load">
    <!-- <mescroll-vue ref="mescroll" :down="mescrollDown" :up="mescrollUp" @init="mescrollInit"> -->
      <div class="containerListContent borderRadius">
        <div
          class="contentList"
          v-for="(item,index) in list"
          :key="index"
          @click="clickListBtn(item.id)"
        >
          <div class="listNameTitle">
            <span style="color:#7a7a7a">操作类型:</span>
            <span style="font-size:14px;">{{item.operation}}</span>
          </div>
          <div class="listNameTitle">
            <span style="color:#7a7a7a">IP地址:</span>
            <span style="font-size:14px;">{{item.ip}}</span>
          </div>
          <div class="listNameTitle">
            <span style="color:#ccc">{{item.gmtCreate}}</span>
          </div>
        </div>
      </div>
    <!-- </mescroll-vue> -->
    </mu-load-more>
    </mu-container>
  </div>
</template>
<script>
import AppBace from "../../../static/ajax";
import MescrollVue from "mescroll.js/mescroll.vue";
export default {
  name: "eventControlList",
  data() {
    return {
      searchValue: "",
      openSimple: false,
      tips: false,
      num: 15,
      refreshing: false,
      loading: false,
      list: [],
      options: ["沙井一期进水口水质监测站"],
      value3: null,
      normal: {
        value1: null,
        value2: null
      },
      position: "",
      status: false,
      pageSize: 20,
      token: sessionStorage.getItem("token"),
      mescroll: null, // mescroll实例对象
      mescrollDown: {}, //下拉刷新的配置. (如果下拉刷新和上拉加载处理的逻辑是一样的,则mescrollDown可不用写了)
      mescrollUp: {
        // 上拉加载的配置.
        callback: this.initdata, // 上拉回调,此处简写; 相当于 callback: function(page, mescroll) { }
        //以下是一些常用的配置,当然不写也可以的.
        page: {
          num: 0, //当前页 默认0,回调之前会加1; 即callback(page)会从1开始
          size: 20 //每页数据条数,默认10
        },
        htmlNodata: '<p class="upwarp-nodata">-- END --</p>',
        noMoreSize: 5, //如果列表已无数据,可设置列表的总数量要大于5才显示无更多数据;
        // 避免列表数据过少(比如只有一条数据),显示无更多数据会不好看
        // 这就是为什么无更多数据有时候不显示的原因
        toTop: {
          //回到顶部按钮
          src: "./static/mescroll/mescroll-totop.png", //图片路径,默认null,支持网络图
          offset: 1000 //列表滚动1000px才显示回到顶部按钮
        },
        empty: {
          //列表第一页无任何数据时,显示的空提示布局; 需配置warpId才显示
          warpId: "xxid", //父布局的id (1.3.5版本支持传入dom元素)
          icon: "./static/mescroll/mescroll-empty.png", //图标,默认null,支持网络图
          tip: "暂无相关数据~" //提示
        }
      }
    };
  },
  beforeRouteEnter(to, from, next) {
    // 如果没有配置回到顶部按钮或isBounce,则beforeRouteEnter不用写
    next(vm => {
      // 找到当前mescroll的ref,调用子组件mescroll-vue的beforeRouteEnter方法
      vm.$refs.mescroll && vm.$refs.mescroll.beforeRouteEnter(); // 进入路由时,滚动到原来的列表位置,恢复回到顶部按钮和isBounce的配置
    });
  },
  beforeRouteLeave(to, from, next) {
    // 如果没有配置回到顶部按钮或isBounce,则beforeRouteLeave不用写
    // 找到当前mescroll的ref,调用子组件mescroll-vue的beforeRouteLeave方法
    this.$refs.mescroll && this.$refs.mescroll.beforeRouteLeave(); // 退出路由时,记录列表滚动的位置,隐藏回到顶部按钮和isBounce的配置
    next();
  },

  methods: {
    mescrollInit(mescroll) {
      this.mescroll = mescroll; // 如果this.mescroll对象没有使用到,则mescrollInit可以不用配置
    },
    search() {
      this.initdata();
    },
    async initdata() {
      var url = "/job/sys-log";
      var pageNum = 1;
      var pageSize = this.pageSize;
      var keyWord = this.searchValue;
      var res = await AppBace.request(
        url,
        "GET",
        {
          pageNum,
          pageSize,
          keyWord
        },
        this.token
      );
      console.log(res);
      this.loading = false;
      this.list = res.data.data.rows;
    },
    clickListBtn(e) {
      this.$router.push({
        path: "/JournalControlListDetails",
        query: {
          id: e
        }
      });
    },
    imgShowClose() {
      //   this.position = "position:absolute;top:400px;";
      this.status = true;
    },
    submitBtn() {
      this.status = false;
    },
    refresh() {
      this.refreshing = true;
      this.$refs.container.scrollTop = 0;
      this.refreshing = false;
      // this.text = this.text === 'List' ? 'Menu' : 'List';

      if (this.pageSize > this.list.length) {
        // this.$toast.warning("已经没有更多数据了！！！");
        this.openSimpleDialog();
        this.loading = false;
      } else {
        this.pageSize += 5;
        this.initdata();
      }
      // this.initdata();
    },
    load() {
      this.loading = true;
      if (this.pageSize > this.list.length) {
        this.$toast.warning("已经没有更多数据了！！！");
        // this.openSimpleDialog();
        this.loading = false;
      } else {
        this.pageSize += 5;
        this.initdata();
      }
    }
  },
  mounted() {
    this.initdata();
  },
  components: {
    MescrollVue
  }
};
</script>
<style scoped>
.listNameTitle {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 0 25px;
}
.containerListContent {
  /* width: 100%; */
  /* margin: 0 10px; */
   /* margin-top: 100px; */
  /* padding: 10px 0; */
  background: white;
  /* width: 100%; */
  /* border-radius: 10px; */
  /* margin-top: 70px; */
}
.contentList {
  border-bottom: 1px solid rgb(243, 241, 241);
  padding:  10px 0;
}
.positionImageSize {
  width: 100%;
  text-align: center;
  background: white;
  position: absolute;
  top: 60px;
  padding: 0 30px;
  padding-bottom: 10px;
}
.imgPosition {
  /* margin-bottom:-20px; */
  /* position: absolute;
    bottom: 0; */
  width: 100%;
  text-align: center;
  position: relative;
  top: -7px;
}
.imgPosition img {
  width: 60px;
}
.flexCenterMain {
  display: flex;
  align-content: center;
  height: 50px;
}
.headerZINDEX {
  width: 100%;
  z-index: 999;
  position: absolute;
  top: 0;
}
.submitStyles {
  width: 100%;
  background: #4ed995;
  height: 40px;
  display: flex;
  color: white;
  font-size: 16px;
  /* align-content: center; */
  justify-content: center;
  align-items: center;
}
.imgSizeDaily img {
  width: 25px;
  height: 25px;
  /* flex: 1; */
}
.imgSizeDaily input {
  border: none;
  outline: none;
  flex: 1;
}
.imgSizeDaily {
  height: 36px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.border {
  /* border: #ccc 1px solid; */
  margin-left: 8px;
  margin-right: 8px;
  padding: 0 10px;
  margin-top: 60px;
  margin-bottom: 10px;
  border-radius: 10px;
  height: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* position: fixed;
  top: 0px; */
}
.borderRadius{
  border-radius: 10px;
}
.headerSearchContent{
  /* margin: 10px; */
}
</style>



